:root {
  --gray-000: #f8f9fa;
  --gray-100: #f1f3f5;
  --gray-200: #e9ecef;
  --gray-300: #dee2e6;
  --gray-400: #ced4da;
  --gray-500: #adb5bd;
  --gray-600: #868e96;
  --gray-700: #495057;
  --gray-800: #343a40;
  --gray-900: #212529;

  --red: #F21B42;
  --pink: #F25645;
  --grape: #be4bdb;
  --purple: #743DFF;
  --indigo: #315ADA;
  --blue: #4C45D7;
  --cyan: #315ADA;
  --teal: #12b886;
  --green: #45F297;
  --yellow: #E3EB1F;
  --orange: #FF6C0A;

  --blue-300: #74c0fc;
  --blue-400: #4dabf7;
  --yellow-100: #fff3bf;

  --body-font: "IBM Plex Mono", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --body-font-size: 16px;
  --body-line-height: 1.5;
  --body-color: var(--gray-300);

  --body-bg: var(--gray-800);


  --link-color: var(--blue-300);
  --link-hover-color: var(--blue-400);

  --heading-color: #fff;

  --border-color: rgba(255,255,255,.15);
  --border-radius: .25rem;

  --code-bg: var(--gray-900);

  --code-font: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --code-color: var(--grape);
  --code-bg: var(--gray-000);

  --spacer: 1rem;
  --spacer-2: calc(var(--spacer) * 1.5);
  --spacer-3: calc(var(--spacer) * 3);
}

